import React from "react";

export default class Kdci_solution extends React.Component {
    constructor(props){
        super(props);
        this._handleItemSort = this._handleItemSort.bind(this);
        this.state = {
            shoppingList: ['Bananas', 'Apples', 'Rice', 'Eggs' , 'GTX 1080Ti', 'Avocado']
        }
    }
      
      
    _handleItemSort(dir, currentIndex) {
        // create new list of items from a spread of our current shoppingList state.
        // we don't want to reference the actual state and mutate it! 
        const shoppingList = [...this.state.shoppingList]

        // references the item we want to move up or down
        let item = shoppingList[currentIndex];

        // pull the item from the list to be moved elsewhere
        shoppingList.splice(currentIndex, 1) 

        // set our items new location based off the desired direction
        let newIdx = dir === "up" ? currentIndex - 1 : currentIndex + 1;

        // add our item at the new index.
        shoppingList.splice(newIdx, 0, item)

        // update our app state
        this.setState({
            shoppingList
        }) 
    }
      
    render() {
        return( 
            <div>
                <h1>UNUM Challenge for Christian Bryant</h1>
                <ol>
                    {this.state.shoppingList.map((item, index) =>(
                        <li data-index = {index} key={index}>
                            {item}
                            <div className='controls'>
                                {index}
                                <button 
                                    disabled={index === 0} 
                                    onClick={ () => this._handleItemSort("up", index) }>UP
                                </button>
                                <button
                                    disabled={this.state.shoppingList.length === index + 1}
                                    onClick={ () => this._handleItemSort("down", index) } >DOWN
                                </button>
                            </div>
                        </li>
                        )
                    )}
                </ol>
            </div>
        );
    }
    }