import React from "react";

export default class Main_page extends React.Component{
	constructor(props){
        super(props);
        this.items_sort = this.items_sort.bind(this);
        this.state = {
            shopping_list: ['Bananas', 'Apples', 'Rice', 'Eggs' , 'GTX 1080Ti', 'Avocado']
        }
	}

	items_sort = (direction, current_index) => {
		const shopping_list = [...this.state.shopping_list];
		let item = shopping_list[current_index];
		shopping_list.splice(current_index, 1);
		let new_index = direction == "up" ? current_index - 1 : current_index + 1;
		shopping_list.splice(new_index, 0, item);
		this.setState({shopping_list});
	}
	
	render(){
		return(
			<section id="main_page">
				<ol>
					{
						this.state.shopping_list.map((value, index) => 
							(
								<li key={index}>{value}
									<button 
										disabled={index === 0}
										onClick={() => this.items_sort("up", index)}
									>UP</button>
									<button 
										disabled={this.state.shopping_list.length === index + 1}
										onClick={() => this.items_sort("down", index)}
									>DOWN</button>
								</li>
							)
						)
					}
				</ol>
			</section>
		)
	}
}
